import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import Styled from 'styled-components';
import { Icon } from '@iconify/react';

import PresetIcons from '../common/PresetIcons';
import { NekoTooltip } from './Tooltip';
import { useClasses } from '../hooks';

// Styled container for the icon to ensure alignment and styling
const StyledIconContainer = Styled.div`
  display: flex;
  align-items: center;
`;

// Function to handle hover color changes
const hoverColor = (color, hoverColor) => {
  if (hoverColor) {
    return `
      &:hover {
        path {
          fill: ${hoverColor};
        }
      }
    `;
  }
  if (color) {
    return `
      &:hover {
        path {
          filter: brightness(1.03);
        }
      }
    `;
  }
};

// Styled component for the Icon, includes styles for hover, spin, and disabled states
const StyledIcon = Styled(Icon)`
  path {
    fill: ${props => props.color};
  }

  ${props => hoverColor(props.color, props.hoverColor)}

  &.neko-clickable {
    cursor: pointer;
  }

  &.spin {
    animation-name: spin;
    animation-duration: 700ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

    @keyframes spin {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }

  &.disabled {
    pointer-events: none;
    opacity: 0.35;
    cursor: default;
  }
`;

// Styled div for icons, ensures proper sizing and alignment
const StyledDiv = Styled.div`
  width: 25px;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    width: auto !important;
    height: 25px !important;
  }
`;

// Color variants for different icon states
const variants = {
  primary: {
    color: 'var(--neko-blue)',
  },
  success: {
    color: 'var(--neko-green)',
  },
  warning: {
    color: 'var(--neko-yellow)',
  },
  danger: {
    color: 'var(--neko-red)',
  }
}

// Main NekoIcon component
const NekoIcon = props => {
  let {
    icon, spinning = false, className = '', title, tooltip, containerStyle, color, hoverColor,
    disabled = false, raw, isBusy = false, variant, ...rest
  } = props;

  const iconColor = variant && variants[variant] ? variants[variant].color : color;
  const iconHoverColor = variant && variants[variant] ? variants[variant].hoverColor : hoverColor;

  // Default sizing if not provided
  if (!rest.width && !rest.height) {
    rest.width = rest.height = 30;
  }

  // Determine final icon based on input type
  const finalIcon = useMemo(() => {
    if (typeof icon === 'string' && PresetIcons[icon]) {
      return PresetIcons[icon];
    }
    return icon;
  }, [icon]);

  // Check if icon is from Iconify
  const isIconifyIcon = useMemo(() => {
    return !!PresetIcons[icon] || typeof icon === 'object';
  }, [icon]);

  // Compile classes for icon
  const iconClassName = useClasses('neko-icon', className, { 'neko-clickable': !!rest.onClick }, { spin: spinning }, { disabled });

  // Render based on different props conditions
  if (tooltip) {
    return (
      <NekoTooltip text={tooltip.text} position={tooltip.position || 'top'}>
        <StyledIconContainer style={containerStyle}>
          <StyledIcon icon={finalIcon} className={iconClassName} color={iconColor} hoverColor={iconHoverColor} {...rest} />
        </StyledIconContainer>
      </NekoTooltip>
    );
  }

  if (raw) {
    if (isIconifyIcon) {
      return <StyledIcon icon={finalIcon} className={iconClassName} color={iconColor} hoverColor={iconHoverColor} {...rest} />;
    }
    return <StyledDiv>{finalIcon}</StyledDiv>;
  }

  if (isBusy && !disabled) {
    return (
      <StyledDiv>
        <img src="/wp-includes/images/spinner-2x.gif" alt="loading" />
      </StyledDiv>
    );
  }

  return (
    <StyledIconContainer style={containerStyle} title={title}>
      {isIconifyIcon && <StyledIcon icon={finalIcon} className={iconClassName}
        color={iconColor} hoverColor={iconHoverColor} {...rest} />}
      {!isIconifyIcon && <StyledDiv className={iconClassName}
        color={iconColor} hoverColor={iconHoverColor} {...rest}>{finalIcon}</StyledDiv>}
      {!tooltip ? null : <NekoTooltip>{tooltip}</NekoTooltip>}
    </StyledIconContainer>
  );
};

// Proptypes for NekoIcon
NekoIcon.propTypes = {
  icon: PropTypes.oneOfType([
    PropTypes.instanceOf(Icon),
    PropTypes.oneOf(['lock', 'lock-open', 'file-undo', 'chevron-double-left', 'chevron-double-right', 'chevron-left', 'chevron-right', 'chevron-down', 'chevron-up', 'pause', 'play', 'replay', 'check', 'stop', 'delete', 'undo', 'alert', 'database', 'pencil', 'tools', 'cog', 'close', 'cat', 'upload'])
  ]),
  color: PropTypes.string,
  spinning: PropTypes.bool,
  className: PropTypes.string,
  tooltip: PropTypes.string,
  raw: PropTypes.bool,
  isBusy: PropTypes.bool,
  variant: PropTypes.string,
};

// Default props for NekoIcon
NekoIcon.defaultProps = {
  icon: undefined,
  color: undefined,
  spinning: false,
  className: '',
  tooltip: undefined,
  raw: undefined,
  isBusy: false,
  variant: undefined,
};

// Export NekoIcon
export { NekoIcon };
